import { Component } from "react";
import { NavLink, Link } from 'react-router-dom'
import Logo from "../assets/images/myteam.svg"



export class MainFooter extends Component {

    render() {

        return (
            <section className="main-footer">
                <div>
                    <div>
                        <Link to="/" className="main-logo"><img src={Logo} alt="logo" /></Link>
                        <nav>
                            <NavLink to="/">home</NavLink>
                            <NavLink to="/">about</NavLink>
                        </nav>
                    </div>
                    <p>
                        <span> 987  Hillcrest Lane  </span>
                        <span> Irvine, CA </span>
                        <span> California 92714 </span>
                        <span> Call Us : 949-833-7432  </span>
                    </p>
                </div>
            </section>
        )
    }
}